<template>
    <div class="overflow-hidden card move-on-hover">
       <div class="coming-soon">
         <h1>Masih Proses Validasi ya kak (  )</h1>
         <p>Admin sedang mengecheck berkas mkamu :D ditunggu 1x24 jam ya kak</p>
         <p>Jika sudah terverifikasi silahkan login ulang ya kak ^^</p>
         <div class="countdown-timer">
           <p>Acara dimulai dalam :</p>
           <div class="timer">
             <div class="timer-item">
               <span class="days">{{ days }}</span>
               <span class="timer-label">Days</span>
             </div>
             <div class="timer-item">
               <span class="hours">{{ hours }}</span>
               <span class="timer-label">Hours</span>
             </div>
             <div class="timer-item">
               <span class="minutes">{{ minutes }}</span>
               <span class="timer-label">Minutes</span>
             </div>
             <div class="timer-item">
               <span class="seconds">{{ seconds }}</span>
               <span class="timer-label">Seconds</span>
             </div>
           </div>
         </div>
       </div>
    </div>
  </template>
     
  <script>
  // eslint-disable-next-line vue/multi-word-component-names
  /* eslint-disable */
  export default {
    data() {
      return {
        launchDate: new Date('2023-05-13T00:00:00'),
        days: '',
        hours: '',
        minutes: '',
        seconds: '',
      }
    },
    created() {
        this.$store.state.hideConfigButton = true;
        this.$store.state.showNavbar = false;
        this.$store.state.showSidenav = false;
        this.$store.state.showFooter = false;
      setInterval(() => {
        const now = new Date().getTime()
        const timeRemaining = this.launchDate - now
  
        this.days = Math.floor(timeRemaining / (1000 * 60 * 60 * 24))
        this.hours = Math.floor(
          (timeRemaining % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        )
        this.minutes = Math.floor((timeRemaining % (1000 * 60 * 60)) / (1000 * 60))
        this.seconds = Math.floor((timeRemaining % (1000 * 60)) / 1000)
      }, 1000)
    },
    beforeUnmount() {
    this.$store.state.hideConfigButton = false;
    this.$store.state.showNavbar = true;
    this.$store.state.showSidenav = true;
    this.$store.state.showFooter = true;
    
  },
    name: 'NotFound',
    
  };
  </script>
  
  <style scoped>
  .not-found {
    text-align: center;
    margin-top: 300px;
  }
  </style>
  