<template>
  <div class="container top-0 position-sticky z-index-sticky">
    <div class="row">
      <div class="col-12">
        <!-- <navbar
          isBlur="blur  border-radius-lg my-3 py-2 start-0 end-0 mx-4 shadow"
          v-bind:darkMode="true"
          isBtn="bg-gradient-success"
        /> -->
      </div>
    </div>
  </div>
  <main class="mt-0 main-content">
    <section>
      <div class="page-header min-vh-100">
        <div class="container">
          <div class="row">
            <div class="mx-auto col-xl-4 col-lg-5 col-md-7 d-flex flex-column mx-lg-0">
              <div class="card card-plain">
                <div class="pb-0 card-header text-center">
                  <img src="@/assets/logos/Mukernas2023.png" class="navbar-brand-img h-10 mx-auto w-40" alt="main_logo">
                  <h4 class="font-weight-bolder">Sign In</h4>
                  <p class="mb-0">Admin Mukernas Fornas Sosma 2023</p>
                </div>
                <div class="card-body">
                  <form role="form" @submit.prevent="submit">
                    <table class="table align-items-center mb-0">
                      <div class="card-body">
                            <div class="col-md-auto mt-2">
                              <label for="example-text-input" class="form-control-label" required
                                >Email</label
                              >
                              <input v-model="data.email" class="form-control" type="text" placeholder="masukkan email admin" />
                            </div>
                            <div class="col-md-auto mt-2">
                              <label for="example-text-input" class="form-control-label" required
                                >Password</label
                              >
                              <input v-model="data.password" class="form-control" type="password" placeholder="masukkan password" />
                            </div>
                        </div>
                    </table>
                    <div class="col-6 text-home px-4 pb-4 pt-0 mt-2">
                      <argon-button type="submit" color="dark" variant="gradient" @click="submit">
                        SignIn
                      </argon-button>
                    </div>
                  </form>               
                </div>

              </div>
            </div>
            <div
              class="top-0 my-auto text-center col-6 d-lg-flex d-none h-100 pe-0 position-absolute end-0 justify-content-center flex-column"
            >
              <div
                class="position-relative bg-gradient-primary h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center overflow-hidden"
                :style="{'background-image': 'url(' + logoUrl + ')', 'background-size': 'cover'}"
              >
                <span class="mask bg-gradient-success opacity-4"></span>
                <!-- <h4
                  class="mt-5 text-white font-weight-bolder position-relative"
                >"Selamat Datang Para Admin Mukernas Fornas Sosma 2023"</h4>
                <p
                  class="text-white position-relative"
                >- Teruslah melangkah sobat, ikuti flow kehidupan ini -</p> -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
/* eslint-disable */
// import {useRouter} from 'vue-router';
// import {reactive} from 'vue';
import axios from 'axios';
import Navbar from "@/examples/PageLayout/Navbar.vue";
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonSwitch from "@/components/ArgonSwitch.vue";
import ArgonButton from "@/components/ArgonButton.vue";
import logo from '@/assets/img/logos/maskot.png'
const body = document.getElementsByTagName("body")[0];

export default {
  name: "signin",
  components: {
    Navbar,
    ArgonInput,
    ArgonSwitch,
    ArgonButton,
  },
  data () {
    return {
      logoUrl: logo,
      imageUrl: 'https://raw.githubusercontent.com/creativetimofficial/public-assets/master/argon-dashboard-pro/assets/img/signin-ill.jpg',
      data: {
          user:{
            email: '',
            password: '',
            role: '',
            verified: '',
            },
        }
    }
  },
  methods: {
      submit() {
        if (!this.data.email || !this.data.password) {
          alert('Harap isi semua field yang dibutuhkan!');
          return;
        }
        const url = `http://127.0.0.1:8000/api/cek_login`;
        const data = {
          email: this.data.email,
          password: this.data.password,

        };

        axios.post(url, data)  
          .then(response => {
            console.log(response);
            const token = response.data.token;
            const userId = response.data.user.id;
            localStorage.setItem('token', token);
            localStorage.setItem('id', userId);
            const userRole = response.data.user.role; 
            const userVerified = response.data.user.verified; 
            if (userRole === 'admin' || userRole === 'superadmin' || userRole === 'berita' || userRole === 'lo' || userRole === 'logistik') {
              this.$router.push('/');
            } else if (userRole === 'user' && userVerified === 0) {
              this.$router.push('/notValidate');
            } else if (userRole === 'user') {
              this.$router.push('/halamanuser');
            }
          })
          .catch(error => {
            console.log(error);
            // handle error here
          });
      },
      autoLogin() {
      const token = localStorage.getItem('token');
      const userId = localStorage.getItem('id');
      if (token && userId) {
        const url = `http://127.0.0.1:8000/api/user/${userId}`;
        const headers = {
          Authorization: `Bearer ${token}`
        };
        axios.get(url, { headers })
          .then(response => {
            const userRole = response.data.role;
            const userVerified = response.data.verified;
            if (userRole === 'admin' || userRole === 'superadmin' || userRole === 'berita' || userRole === 'lo' || userRole === 'logistik') {
              this.$router.push('/');
            } else if (userRole === 'user' && userVerified === 0) {
              this.$router.push('/notValidate');
            } else if (userRole === 'user') {
              this.$router.push('/halamanuser');
            }
          })
          .catch(error => {
            console.log(error);
            // handle error here
          });
        }
      }
    },
  created() {
    this.$store.state.hideConfigButton = true;
    this.$store.state.showNavbar = false;
    this.$store.state.showSidenav = false;
    this.$store.state.showFooter = false;
    body.classList.remove("bg-gray-100");
    this.autoLogin(); 
  },
  beforeUnmount() {
    this.$store.state.hideConfigButton = false;
    this.$store.state.showNavbar = true;
    this.$store.state.showSidenav = true;
    this.$store.state.showFooter = true;
    body.classList.add("bg-gray-100");
  },
  
};
</script>
