<template>
    <div class="card">
      <div class="card-header pb-0">
        <h6>Data Penjemputan dan LO</h6>
      </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0 overflow-auto">
          <table class="table align-items-center mb-0">
            <thead>
              <tr>
                <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Acount</th>
                <th
                  class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
                >Transportasi</th>
                <th
                  class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
                >Penjemput, LO</th>
                <th
                  class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
                >Klotter Penjemputan</th>
                <th class="text-secondary opacity-7"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="data in filteredData" :key="data.id" >
                <td>
                  <div class="d-flex px-2 py-1">
                    <div>
                      <img
                        :src="'http://127.0.0.1:8000/storage/' + data.photo"
                        class="avatar avatar-sm me-3"
                        alt="user1"
                      />
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{data.name}}</h6>
                      <p class="text-xs text-secondary mb-0">{{data.email}}</p>
                    </div>
                  </div>
                </td>
                <td >
                  <div>
                    <p class="text-xs font-weight-bold mb-0">{{data.transport}}</p>
                  </div>
                </td>
                <td  class="align-middle text-center text-sm">
                  <div>
                    <p class="text-xs font-weight-bold mb-0">{{data.penjemput}},</p>
                    <p class="text-xs font-weight-bold mb-0">{{data.lo}}</p>    
                  </div>
                </td>
                <td class="align-middle text-center">
                  <span class="text-secondary text-xs font-weight-bold">{{data.klotter}}</span>
                </td>
                <td class="align-middle">
                  <a
                    href="javascript:;"
                    class="text-secondary font-weight-bold text-xs"
                    data-toggle="tooltip"
                    data-original-title="Edit user"
                    @click="redirectToValidation(data.id)"
                  >View</a>
                </td>
              </tr >
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  /* eslint-disable */
  import axios from 'axios';

  export default {
    name: "authors-table",
    props: ['id'],
    data() {
    return {
      data: {
        data: []
      },
    };
  },
  computed: {
    filteredData() {
      return this.data.data.filter(user => user.verified === 1);
    }
  },
  mounted() {
    axios.get('http://127.0.0.1:8000/api/users')
      .then(response => { 
        this.data = response.data;  
      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    redirectToValidation(id) { 
      axios.get(`http://127.0.0.1:8000/api/users/${id}`)
        .then(response => {
          const user = response.data;
          this.$router.push({
            name: "Edit Data", 
            params: {
              id: id,
            },
            props: {
              id: id
            }
          });
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
  };
  </script>
  
  <style>
  .table-responsive{
    height: 500px;
    overflow-x: scroll;
    overflow-y: hidden;
  }
  </style>