<template>
  <nav
    class="navbar navbar-main navbar-expand-lg px-0 mx-4 shadow-none border-radius-xl"
    :class="
      this.$store.state.isRTL ? 'top-0 position-sticky z-index-sticky' : ''
    "
    v-bind="$attrs"
    id="navbarBlur"
    data-scroll="true"
  >
    <div class="px-3 py-1 container-fluid">
      <breadcrumbs :currentPage="currentRouteName" textWhite="text-white" />

      <div
        class="mt-2 collapse navbar-collapse mt-sm-0 me-md-0 me-sm-4"
        :class="this.$store.state.isRTL ? 'px-0' : 'me-sm-4'"
        id="navbar"
      >
        <div
          class="pe-md-3 d-flex align-items-center"
          :class="this.$store.state.isRTL ? 'me-md-auto' : 'ms-md-auto'"
        >
          <div class="input-group">
           
          </div>
        </div>
        <ul class="navbar-nav justify-content-end">
          <div class="d-flex px-0 py-1">
            <div>
                 <img :src="(data.photo == '0') ? ('http://127.0.0.1:8000/storage/' + data.photo) : ('http://127.0.0.1:8000/storage/kendaraanpribadi.jpg')"
                       alt="user1" class="avatar avatar-sm me-3">
                </div>
              </div>
          <li class="nav-item d-xl-none ps-3 d-flex align-items-center">
            <a
              href="#"
              @click="toggleSidebar"
              class="p-0 nav-link text-white"
              id="iconNavbarSidenav"
            >
              <div class="sidenav-toggler-inner">
                <i class="sidenav-toggler-line bg-white"></i>
                <i class="sidenav-toggler-line bg-white"></i>
                <i class="sidenav-toggler-line bg-white"></i>
              </div>
            </a>
          </li>
          <li class="px-3 nav-item d-flex align-items-center">
            <a class="p-0 nav-link text-white" @click="toggleConfigurator">
              <i class="cursor-pointer fa fa-cog fixed-plugin-button-nav"></i>
            </a>
          </li>
          <li class="nav-item dropdown d-flex align-items-center">
            <form role="form" @submit.prevent="submit">
            <a class="p-0 nav-link text-white" @click="submit">
              <i class="cursor-pointer fa fa-sign-out fixed-plugin-button-nav" style="color: #ffffff;"></i>
              <span class="d-sm-inline d-none" style="margin-left:4px">LogOut</span>
            </a>
          </form>
            <ul
              class="px-2 py-3 dropdown-menu dropdown-menu-end me-sm-n4"
              :class="showMenu ? 'show' : ''"
              aria-labelledby="dropdownMenuButton"
            >
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>
<script>
/* eslint-disable */
import axios from 'axios';
import Breadcrumbs from "../MyBreadcrumbs.vue";
import { mapMutations, mapActions } from "vuex";

export default {
  name: "navbar",
  data() {
    return {
      showMenu: false,
      data: {
        user:{
            name: '',
            email: '',
            phone: '',
            tiket: '',
            photo: '',
            ktm: '',
            transfer: '',
            verified: '',
            role:'',
        } 
      },
    } 
  },
  mounted() {
  const userId = localStorage.getItem('id');
  if (userId) {
    axios.get(`http://127.0.0.1:8000/api/users/${userId}`, {
      headers: {
        Authorization: 'Bearer ' + localStorage.getItem('token')
      }
    })
      .then(response => {
        console.log(response);
        this.data.user = response.data.data;
      })
      .catch(error => {
        console.error(error);
      });
    }
  },
  props: ["minNav", "textWhite"],
  created() {
    this.minNav;
  },
  methods: {
    ...mapMutations(["navbarMinimize", "toggleConfigurator"]),
    ...mapActions(["toggleSidebarColor"]),

    toggleSidebar() {
      this.toggleSidebarColor("bg-gradient-success");
      this.navbarMinimize();
    },
    submit() {
      const token = localStorage.getItem('token');
      if (token) {
        axios.post('http://127.0.0.1:8000/api/auth/logout', {}, {
          headers: {
            Authorization: `Bearer ${token}`
          }
        })
        .then(response => {
          console.log(response);
          localStorage.removeItem('token'); 
          localStorage.removeItem('id'); 
          this.$router.push('/signin'); 
        })
        .catch(error => {
          console.log(error);
        });
      }
    }
  },
  components: {
    Breadcrumbs
  },
  computed: {
    currentRouteName() {
      return this.$route.name;
    }
  }
};
</script>
