<template>
  <div class="py-4 container-fluid">
    <div class="row">
      <div class="col-lg-12">
        <div class="row">
          <div class="col-lg-3 col-md-6 col-12">
            <card
              :title="stats.pendaftar.title"
              :value="stats.pendaftar.value"
              :percentage="stats.pendaftar.percentage"
              :iconClass="stats.pendaftar.iconClass"
              :iconBackground="stats.pendaftar.iconBackground"
              :detail="stats.pendaftar.detail"
              directionReverse
            ></card>
          </div>
          <div class="col-lg-3 col-md-6 col-12">
            <card
              :title="stats.users.title"
              :value="stats.users.value"
              :percentage="stats.users.percentage"
              :iconClass="stats.users.iconClass"
              :iconBackground="stats.users.iconBackground"
              :detail="stats.users.detail"
              directionReverse
            ></card>
          </div>
          <div class="col-lg-3 col-md-6 col-12">
            <card
              :title="stats.admin.title"
              :value="stats.admin.value"
              :percentage="stats.admin.percentage"
              :iconClass="stats.admin.iconClass"
              :iconBackground="stats.admin.iconBackground"
              :percentageColor="stats.admin.percentageColor"
              :detail="stats.admin.detail"
              directionReverse
            ></card>
          </div>
          <div class="col-lg-3 col-md-6 col-12">
            <card
              :title="stats.univ.title"
              :value="stats.univ.value"
              :percentage="stats.univ.percentage"
              :iconClass="stats.univ.iconClass"
              :iconBackground="stats.univ.iconBackground"
              :detail="stats.univ.detail"
              directionReverse
            ></card>
          </div>
        </div>
        <div class="row">
          <div class="col-lg-7 mb-lg">
            <!-- line chart -->
            <div class="card z-index-2">
              <gradient-line-chart />
            </div>
          </div>
          <div class="col-lg-5">
            <Bumper />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import axios from "axios";
import Card from "@/examples/Cards/Card.vue";
import GradientLineChart from "@/examples/Charts/GradientLineChart.vue";
import Bumper from "./components/Bumper.vue";
import CategoriesCard from "./components/CategoriesCard.vue";

export default {
  name: "dashboard",
  data() {
    return {
      stats: {
        pendaftar: {
          title: "Jumlah Pendaftar",
          value: '',
          iconClass: "ni ni-world",
          iconBackground: "bg-gradient-primary",
        },
        users: {
          title: "Users Tervalidasi",
          value: '',
          iconClass: "ni ni-world",
          iconBackground: "bg-gradient-danger",
        },
        admin: {
          title: "Total Admin",
          value: "",
          iconClass: "ni ni-paper-diploma",         
          iconBackground: "bg-gradient-success",
        },
        univ: {
          title: "Universitas Partisipan",
          value: '',
          iconClass: "ni ni-paper-diploma",
          iconBackground: "bg-gradient-warning",
        },
      },
    };
  },
  mounted() {
    axios.get("http://127.0.0.1:8000/api/users")
      .then((response) => {
        this.stats.pendaftar.value = response.data.data.filter((el) => el.role === 'user').length;
        this.stats.admin.value = response.data.data.filter(el => ['admin', 'logistik', 'superadmin', 'berita', 'lo'].includes(el.role)).length;
        this.stats.users.value = response.data.data.filter((el) => el.verified === 1).length;
        const institutions = response.data.data.map((el) => el.institution); 
        const filteredInstitutions = institutions.filter((inst) => inst !== null);
        const uniqueInstitutions = new Set(filteredInstitutions);
        this.stats.univ.value = uniqueInstitutions.size;
        console.log(filteredInstitutions.length);
      })
      .catch((error) => {
        console.log(error);
      });
  },
  components: {
    Card,
    GradientLineChart,
    Bumper,
    CategoriesCard,
  },
};
</script>
