<template>
  <main>
    <div class="py-4 container-fluid">
      <div class="row">
        <div class="col-md-8">
          <div class="card">
            <div class="card-header pb-0">
              <div class="d-flex align-items-center">
                <p class="mb-0">Edit Profile</p>
                <argon-button color="success" size="sm" class="ms-auto"
                  >Settings</argon-button
                >
              </div>
            </div>
            <table class="table align-items-center mb-0">
            <tbody>
              <tr>
                <div class="card-body p-">
                  <div class="row gx-4">
                    <div class="col-auto">
                      <div class="avatar avatar-xl position-relative">
                        <img
                          src="../assets/img/team-4.jpg"
                          alt="profile_image"
                          class="shadow-sm w-100 border-radius-lg"
                        />
                      </div>
                    </div>
                    <div class="col-auto my-auto">
                      <div class="h-100">
                        <h5 class="mb-1">{{data.user.name}}</h5>
                        <span :class="(data.user.role === 'admin') ? 'badge badge-sm bg-gradient-primary' :
                            (data.user.role === 'logistik') ? 'badge badge-sm bg-gradient-secondary' :
                            (data.user.role === 'superadmin') ? 'badge badge-sm bg-gradient-info' :
                            (data.user.role === 'berita') ? 'badge badge-sm bg-gradient-warning' :
                            (data.user.role === 'lo') ? 'badge badge-sm bg-gradient-danger' : ''">
                        {{ (data.user.role === 'admin') ? 'Admin' :
                          (data.user.role === 'logistik') ? 'Logistik' :
                          (data.user.role === 'superadmin') ? 'Super Admin' :
                          (data.user.role === 'berita') ? 'Berita' :
                          (data.user.role === 'lo') ? 'LO' : '' }}
                      </span>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="card-body">
                  <p class="text-uppercase text-sm">User Information</p>
                  <div class="row">
                    <div class="col-md-6">
                      <label for="example-text-input" class="form-control-label"
                        >Username</label
                      >
                      <h5 style="margin-left:4px" class="text-sm ">{{data.user.name}}</h5>
                    </div>
                    <div class="col-md-6">
                      <label for="example-text-input" class="form-control-label"
                        >Email address</label
                      >
                      <h5 style="margin-left:4px" class="text-sm ">{{data.user.email}}</h5>
                    </div>
                  </div>
                </div>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="col-md-4">
          <div>
          <Bumper />
          </div>
        </div>
        
      </div>
    </div>
  </main>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import ProfileCard from "./components/ProfileCard.vue";
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonButton from "@/components/ArgonButton.vue";
import Bumper from "@/views/components/Bumper.vue"
import Notes from "@/views/components/Berita.vue"

const body = document.getElementsByTagName("body")[0];

export default {
  name: "profile",
  components: { ProfileCard, ArgonInput, ArgonButton, Bumper, Notes},
  data() {
    return {
      showMenu: false,
      data: {
        user:{
            name: '',
            email: '',
            phone: '',
            tiket: '',
            photo: '',
            ktm: '',
            transfer: '',
            verified: '',
            role:'',
        } 
      },
    } 
  },

  mounted() {
  const userId = localStorage.getItem('id');
  if (userId) {
    axios.get(`http://127.0.0.1:8000/api/users/${userId}`, {
      headers: {
        Authorization: 'Bearer ' + localStorage.getItem('token')
      }
    })
      .then(response => {
        console.log(response);
        this.data.user = response.data.data;
      })
      .catch(error => {
        console.error(error);
      });
    }
  }
};
</script>
