<template>
  <div class="card">
    <div class="row">
      <div class="col-6 d-flex align-items-center px-5 pb-0 pt-1">
        <h6 class="mb-0">Tambah Berita</h6>
      </div>
      <div class="col-6 text-end px-5 pb-2 pt-0 mt-4" @click="$router.push('/berita/addberita')">
        <argon-button color="dark" variant="gradient">
          <i class="fas fa-plus me-2"></i>
          Tambah Berita
        </argon-button>
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0 overflow-auto">
        <table class="table align-items-center mb-0">
          <thead>
            <tr>
              <th 
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Judul Berita</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7" 
              >Deskripsi</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7" 
              >Foto/Video</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Ubah</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="data in data.data" :key="data.id">
              
              <td>
                <div class="d-flex px-2 py-1">
                  <div class="d-flex flex-column justify-content-center">
                    <p class="mb-0 text-sm">{{ data.judul.substring(0, 20) }}{{ data.judul.length > 20 ? '...' : '' }}</p>
                  </div>
                </div>
              </td>
              <td>
                <p class="mb-0 text-sm">{{ data.deskripsi.substring(0, 100)}} {{  data.deskripsi.length > 100 ? '...' : '' }}</p>
             </td> 
              <td class="align-middle text-center text-sm">
                <img :src="'http://127.0.0.1:8000/storage/file/' + data.file" alt="Gambar Berita" height="100">
              </td>
              <td class="align-middle text-center">
                <row>
                  <a class="btn btn-link text-dark px-2 mb-0" href="javascript:;">
                    <i class="fas fa-pencil-alt text-dark me-2" aria-hidden="true"></i>Edit
                  </a>
                  <a class="btn btn-link text-danger text-gradient px-2 mb-0" href="javascript:;" @click="this.deleteBerita(data.id)">
                    <i class="far fa-trash-alt me-2" aria-hidden="true"></i>Delete
                  </a>
                </row>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
  
<script>
/* eslint-disable */
import ArgonButton from "@/components/ArgonButton.vue";
import axios from 'axios';

export default {
  name: "Berita",
  components: {
    ArgonButton,
  },
  data() {
    return {
      data: {
        data: []
      },
    };
  },
  mounted() {
    axios.get('http://127.0.0.1:8000/api/beritas')
      .then(response => { 
        this.data = response.data;  
      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    deleteBerita(id) {
      axios.delete(`http://127.0.0.1:8000/api/beritas/${id}`)
        .then(response => {
          console.log(response);
          // reload data
          axios.get('http://127.0.0.1:8000/api/beritas')
            .then(response => { 
              this.data = response.data;  
            })
            .catch(error => {
              console.log(error);
            });
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style>
.table-responsive{
  height: 500px;
  overflow-x: scroll;
  overflow-y: hidden;
}
</style>