import { createRouter, createWebHistory } from "vue-router";
// import store from '../store';
import axios from "axios";
import Dashboard from "../views/Dashboard.vue";
import Tables from "../views/UserPanel.vue";
import Billing from "../views/Logistik.vue";
import AddBerita from "../views/Berita.vue";
import Validasi from "../views/ValidasiAkun.vue";
import Profile from "../views/Profile.vue";
import ProfileUser from "../views/ProfileUser.vue";
import Absensi from "../components/ComingsoonAll.vue";
import AbsensiUser from "../components/ComingsoonAll.vue";
import Signin from "../views/Signin.vue";
import Dashboarduser from "../views/halamanuser/MyDashboardUser.vue";
import NotFound from '../components/NotFound.vue';
import ValidasiUser from "../views/FormValidation.vue";
import notValidate from "../components/notValidate.vue";

/* eslint-disable */ 

const routes = [
  {
    path: "/",
    name: "/",
    redirect: "/dashboard-default",
  },
  
  {
    path: "/absensiuser",
    name: "Absensi User",
    component: AbsensiUser,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["user"] 
    }
  },
  {
    path: "/halamanuser",
    name: "Dashboard User",
    component: Dashboarduser,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["user"] 
    }
  },
  {
    path: "/dashboard-default",
    name: "Dashboard",
    component: Dashboard,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["admin", "superadmin", "lo", "berita", "logistik"] 
    }
  },
  {
    path: "/validasiAkun",
    name: "Validasi Akun",
    component: Validasi,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["admin", "superadmin"] 
    }
  },
  {
    path: "/validasi",
    name: "Validasi",
    component: ValidasiUser,

  },
  {
    path: "/validasiakun/formdata/:id",
    name: "Form Data",
    component: () => import("../views/FormData.vue"),

    props: true,
  },
  {
    path: "/adminpanel",
    name: "Admin Panel",
    component: Tables,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin"] 
    }
  },
  {
    path: "/adminpanel/addadmin",
    name: "Tambah Admin",
    component: () => import("../views/AddAdmin.vue"),
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin"] 
    }
  },
  {
    path: "/logistik",
    name: "Logistik",
    component: Billing,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin", "logistik"] 
    }
  },
  {
    path: "/logistik/view/:id",
    name: "Edit Data",
    component: () => import("../views/LogistikView.vue"),
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin", "logistik"] 
    },
    props: true,
  },
  {
    path: "/berita",
    name: "Berita",
    component: AddBerita,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin", "berita"] 
    }
  },
  {
    path: "/berita/addberita",
    name: "Tambah Berita",
    component: () => import("../views/AddBerita.vue"),
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin", "berita"] 
    }
  },
  {
    path: "/profileuser",
    name: "Profile User",
    component: ProfileUser,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["user"] 
    }
  },
  {
    path: "/profile",
    name: "Profile",
    component: Profile,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["admin", "superadmin", "lo", "berita", "logistik"] 
    }
  },
  {
    path: "/signin",
    name: "Signin",
    component: Signin,

  },
  {
    path: "/notValidate",
    name: "Belum Tervalidasi",
    component: notValidate,
  },
  {
    path: "/absensi",
    name: "Absensi",
    component: Absensi,
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin"] 
    }
  },
  {
    path: "/absensi/addabsensi",
    name: "Tambah Absensi",
    component: () => import("../views/AddAbsensi.vue"),
    meta: { 
      requiresAuth: true, 
      allowedRoles: ["superadmin"] 
    }
  },
  {
    path: '/:pathMatch(.*)*',
    component: NotFound,
  }
  
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    const token = localStorage.getItem("token");
    const userId = localStorage.getItem("id");

    // jika tidak ada token atau userId pada localStorage
    if (!token || !userId) {
      return next({ path: "/login" });
    }

    // ambil data user dari API
    axios.get(`http://127.0.0.1:8000/api/users/${userId}`, {
      headers: { Authorization: `Bearer ${token}` },
    })
      .then(response => {
        const userRole = response.data.data.role;
        // jika userRole tidak termasuk dalam allowedRoles pada meta
        if (!to.meta.allowedRoles.includes(userRole)) {
          return next({ path: "/:pathMatch(.*)*" });
        }
        // jika userRole termasuk dalam allowedRoles pada meta
        next();
        console.log(response.data.data.role);
      })
      .catch(() => {
        // jika terjadi error saat mengambil data user dari API
        return next({ path: "/:pathMatch(.*)*" });
      });
  } else {
    next();
  }
});

export default router;
