<template>
 <div class="overflow-hidden card move-on-hover" style="margin: 3rem;">
    <div class="coming-soon">
      <h1>Coming Soon</h1>
      <p>Ditunggu yaa masih dalam pembuatan</p>
      <p>Insyaallah fitur sudah tersedia dalam waktu berikut ya teman temann</p>
      <div class="countdown-timer">
        <p>Launch Fitur:</p>
        <div class="timer">
          <div class="timer-item">
            <span class="days">{{ days }}</span>
            <span class="timer-label">Days</span>
          </div>
          <div class="timer-item">
            <span class="hours">{{ hours }}</span>
            <span class="timer-label">Hours</span>
          </div>
          <div class="timer-item">
            <span class="minutes">{{ minutes }}</span>
            <span class="timer-label">Minutes</span>
          </div>
          <div class="timer-item">
            <span class="seconds">{{ seconds }}</span>
            <span class="timer-label">Seconds</span>
          </div>
        </div>
      </div>
    </div>
 </div>
  </template>
  
  <script>
  // eslint-disable-next-line vue/multi-word-component-names
  export default {
    data() {
      return {
        launchDate: new Date('2023-04-09T00:00:00'),
        days: '',
        hours: '',
        minutes: '',
        seconds: '',
      }
    },
    created() {
      setInterval(() => {
        const now = new Date().getTime()
        const timeRemaining = this.launchDate - now
  
        this.days = Math.floor(timeRemaining / (1000 * 60 * 60 * 24))
        this.hours = Math.floor(
          (timeRemaining % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        )
        this.minutes = Math.floor((timeRemaining % (1000 * 60 * 60)) / (1000 * 60))
        this.seconds = Math.floor((timeRemaining % (1000 * 60)) / 1000)
      }, 1000)
    },
  }
  </script>
  
 
<style>
.coming-soon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: sans-serif;
  margin: 2rem;
}

.coming-soon h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.coming-soon p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.countdown-timer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  margin: 2rem;
}

.timer {
  display: flex;
}

.timer-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 1rem;
}

.timer-item .timer-label {
  font-size: 0.75rem;
}
</style>
  