<template>
  <div class="card">
    <div class="row">
      <div class="col-6 d-flex align-items-center px-5 pb-0 pt-1">
        <h6 class="mb-0">Admin Panel</h6>
      </div>
      <div class="col-6 text-end px-5 pb-2 pt-0 mt-4" @click="$router.push('/adminpanel/addadmin')">
        <argon-button color="dark" variant="gradient">
          <i class="fas fa-plus me-2"></i>
          Tambah Admin
        </argon-button>
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0 overflow-auto">
        <table class="table align-items-center mb-0">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Acount</th>
              
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >Email</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Role</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >ID</th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="data in filteredData" :key="data.id">
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <img :src="(data.photo == '0') ? ('http://127.0.0.1:8000/storage/' + data.photo) : ('http://127.0.0.1:8000/storage/kendaraanpribadi.jpg')"
                       alt="user1" class="avatar avatar-sm me-3">
                  </div>
                    <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{data.name}}</h6>
                  </div>
                </div>
              </td>
              <td>
                  <p class="text-xs text-secondary mb-0">{{data.email}}</p>
              </td>
              <td class="align-middle text-center text-sm">
                <span :class="(data.role === 'admin') ? 'badge badge-sm bg-gradient-primary' :
              (data.role === 'logistik') ? 'badge badge-sm bg-gradient-secondary' :
              (data.role === 'superadmin') ? 'badge badge-sm bg-gradient-info' :
              (data.role === 'berita') ? 'badge badge-sm bg-gradient-warning' :
              (data.role === 'lo') ? 'badge badge-sm bg-gradient-danger' : ''">
          {{ (data.role === 'admin') ? 'Admin' :
            (data.role === 'logistik') ? 'Logistik' :
            (data.role === 'superadmin') ? 'Super Admin' :
            (data.role === 'berita') ? 'Berita' :
            (data.role === 'lo') ? 'LO' : '' }}
        </span>

              </td>
              <td class="align-middle text-center">
                <span class="text-secondary text-xs font-weight-bold">{{data.id}}</span>
              </td>
              <td class="align-middle text-center">
                <row>
                  <a class="btn btn-link text-danger text-gradient px-2 mb-0" href="javascript:;" @click="this.deleteAdmin(data.id)">
                    <i class="far fa-trash-alt me-2" aria-hidden="true"></i>Delete
                  </a>
                </row>
              </td>
            </tr>
            
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import ArgonButton from "@/components/ArgonButton.vue";

export default {
  name: "authors-table",
  components: {
    ArgonButton,
  },
  data() {
    return {
      data: {
        data: []
      },
      isChecked: false
    };
  },
  mounted() {
    axios.get('http://127.0.0.1:8000/api/users')
      .then(response => { 
        this.data = response.data;  
        console.log(this.data.data.verified)
      })
      .catch(error => {
        console.log(error);
      });
  },
  computed: {
    filteredData() {
       return this.data.data.filter(user => ['admin', 'logistik', 'superadmin', 'berita', 'lo'].includes(user.role));
    }   
  },
  methods: {
    deleteAdmin(id) {
      if (confirm('Apakah Anda yakin ingin menghapus data ini?')) {
        axios.delete(`http://127.0.0.1:8000/api/delete/${id}`)
          .then(response => {
            console.log(response);
            // reload data
            axios.get('http://127.0.0.1:8000/api/users')
              .then(response => { 
                this.data = response.data;  
              })
              .catch(error => {
                console.log(error);
              });
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    redirectToValidation(id) { 
      axios.get(`http://127.0.0.1:8000/api/users/${id}`)
        .then(response => {
          const user = response.data;
          this.$router.push({
            name: "Tambah Admin", 
            params: {
              id: id,
            },
            props: {
              id: id
            }
          });
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
};
</script>
<style>
.table-responsive{
  height: 500px;
  overflow-x: scroll;
  overflow-y: hidden;
}
</style>