<template>
  <div class="card">
    <div class="card-header pb-0">
      <h6>User Validation</h6>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0 overflow-auto">
        <table class="table align-items-center mb-0">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Acount</th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >Transportasi</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Status</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Universitas</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >Alergi</th>
              <th
                class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >No Hp</th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="data in filteredData" :key="data.id">
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <a :href="'http://127.0.0.1:8000/storage/' + data.photo">
                    <img
                      :src="'http://127.0.0.1:8000/storage/' + data.photo"
                      class="avatar avatar-sm me-3"
                    />
                  </a>
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{data.name}}</h6>
                    <p class="text-xs text-secondary mb-0">{{data.email}}</p>
                  </div>
                </div>
              </td>
              <td>
                <p class="text-xs font-weight-bold mb-0">{{data.transport}}</p>
              </td>
              <td class="align-middle text-center">
                <span :class="data.verified ? 'badge badge-sm bg-gradient-primary' : 'badge badge-sm bg-gradient-secondary'">
                  {{ data.verified ? 'Aktif' : 'Belum Aktif' }}
                </span> 
              </td>              
              <td class="align-middle text-center">
                <span class="text-secondary text-xs font-weight-bold">{{data.institution}}</span>
              </td>
              <td class="align-middle text-center">
                <span class="text-secondary text-xs font-weight-bold">{{data.alergi}}</span>
              </td>
              <td class="align-middle text-center">
                <span class="text-secondary text-xs font-weight-bold">{{data.phone}}</span>
              </td>
              <td class="align-middle">
                <a 
                href="javascript:;"
                class="text-secondary font-weight-bold text-xs"
                data-toggle="tooltip"
                data-original-title="Edit user"
                @click="redirectToValidation(data.id)"
              >
                View
              </a>              
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';

export default {
  name: "authors-table",
  props: ['id'],
  data() {
    return {
      data: {
        data: []
      },
      isChecked: false
    };
  },
  mounted() {
    axios.get('http://127.0.0.1:8000/api/users')
      .then(response => { 
        this.data = response.data;  
        console.log(this.data.data.verified)
      })
      .catch(error => {
        console.log(error);
      });
  },
  computed: {
    filteredData() {
      return this.data.data.filter(user => user.role == 'user');
      }
  },
  methods: {
    redirectToValidation(id) { 
      axios.get(`http://127.0.0.1:8000/api/users/${id}`)
        .then(response => {
          const user = response.data;
          this.$router.push({
            name: "Form Data", 
            params: {
              id: id,
            },
            props: {
              id: id
            }
          });
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
};
</script>

<style>
.table-responsive{
  height: 500px;
  overflow-x: scroll;
  overflow-y: hidden;
}

</style>